import { NextRequest, NextResponse } from 'next/server'
import { db } from '@/db'
import { invoices } from '@/db/schema'

export async function POST(req: NextRequest) {
  try {
    const body = await req.json()

    const { customer, amount, taxRate, dueDate, description } = body

    if (!customer || amount === undefined) {
      return NextResponse.json({ error: 'customer und amount sind erforderlich' }, { status: 400 })
    }

    const taxAmount = amount * (taxRate / 100)
    const totalAmount = amount + taxAmount

    const invoice = await db
      .insert(invoices)
      .values({
        customer,
        amount,
        taxRate,
        taxAmount,
        totalAmount,
        dueDate: dueDate || null,
        description: description || null,
        status: 'open'
      })
      .returning()

    return NextResponse.json(invoice[0], { status: 201 })
  } catch (error) {
    console.error('Error creating invoice:', error)
    return NextResponse.json({ error: 'Fehler beim Erstellen der Rechnung' }, { status: 500 })
  }
}

export async function GET(req: NextRequest) {
  try {
    const allInvoices = await db.select().from(invoices).all()
    return NextResponse.json(allInvoices)
  } catch (error) {
    console.error('Error fetching invoices:', error)
    return NextResponse.json({ error: 'Fehler beim Abrufen der Rechnungen' }, { status: 500 })
  }
}
